/* Global */
:root{
    --main-color:#f1f1f1;
    --second-color: #0e2a43;
    --third-color: #6dd4ac;
    --widget-bg: #0e2a43 ;
}
[data-theme=dark_blue]{
    --primary: var(--main-color);
    --dt-primary-color: var(--main-color);
    --blue-avatar-bg: var(--main-color);
    --blue-avatar-color: var(--main-color);
    --blue: var(--main-color)
}

[data-theme=dark_blue] .page-container,
[data-theme=dark_blue] .layout-main-section {
    /* background-color: var(--widget-bg); */
    background-color: #f1f1f1;
}
[data-theme=dark_blue] ul li a:hover {
    background-color: var(--second-color);
    color: #fff;
}
/* Main */
[data-theme=dark_blue] body {
	font-size: 14px;
	background-color: #F1F1F1;
}
[data-theme=dark_blue]a {
    color: #666;
	font-weight: 500;
}
[data-theme=dark_blue] .small, small {
    font-size: 95%;
}
[data-theme=dark_blue] .h6.uppercase, h6.uppercase {
    font-weight: 600;
	font-size: 11.5px;
}

/* Navigation Bar */
[data-theme=dark_blue] .standard-sidebar-item.selected{
    background: linear-gradient(to right, #0e2a43, var(--main-color));
}
[data-theme=dark_blue] .standard-sidebar-item.selected a{
    color: #fff;
}
[data-theme=dark_blue] .standard-sidebar-item.selected a svg{
    fill: #fff!important;
}
[data-theme=dark_blue] .standard-sidebar-item:hover{
    background-color: var(--second-color);
}
[data-theme=dark_blue] .standard-sidebar-item:hover a,
[data-theme=dark_blue] .standard-sidebar-item:hover svg{
    color: #fff!important;
    stroke: white!important;
    fill: white!important;
}



/* navbar  */
[data-theme=dark_blue] .navbar {
    /* background: linear-gradient(to right, var(--main-color), var(--second-color)) !important; */
    background-color: #204E84;
    border: none !important;
}
[data-theme=dark_blue] .navbar-nav>li>a{
	color:#fff !important;
}
[data-theme=dark_blue] .navbar-nav>li>a:hover{
	color:#ddd !important;
}
[data-theme=dark_blue] .navbar-brand:hover {
	color:#ddd !important;
}

/* Widgets */
[data-theme=dark_blue] .widget {
    /* background-color: var(--widget-bg); */
    background-color: #f1f1f1;
	border: none;
	box-shadow: 0px 3px 12px 0px var(--widget-bg), 0 3px 3px -2px #461a7714;
}
[data-theme=dark_blue] .ce-block__content > .widget {
    background-color:none !important
}

[data-theme=dark_blue] .widget.shortcut-widget-box .widget-head .widget-title i {
    /* color: var(--third-color); */
    color: #0e2a43!important;
}
[data-theme=dark_blue] .widget.shortcut-widget-box .widget-head .widget-title:hover span{
    /* color: var(--main-color); */
    color: #E2CB7A;
}
[data-theme=dark_blue] .widget-body .link-item:hover{
    color: #fff;
    background: var(--third-color);
}
/* Sidebar */
[data-theme=dark_blue] .desk-sidebar .desk-sidebar-item {
    font-size: 13px;
    font-weight: 600;
	color: var(--widget-bg);
}
[data-theme=dark_blue] .layout-side-section {
    font-size: 14px;
}
[data-theme=dark_blue] .uppercase {
    color:var(--third-color);
}

/* Dropdowns */
[data-theme=dark_blue] .dropdown-menu>li>a {
    font-size: 13px;
	color:#666;
	font-weight: 500;
}
[data-theme=dark_blue] .awesomplete > ul > li[aria-selected=true]{
    background-color: transparent;
}
[data-theme=dark_blue] .awesomplete > #awesomplete_list_1 > li:hover{
    background: linear-gradient(to right,#0e2a43, var(--main-color));
}
[data-theme=dark_blue] .awesomplete > ul > li:hover a{
    color: #fff;
}
/* Page Heading */
[data-theme=dark_blue] .page-head {
    border-bottom: none !important;
    box-shadow: 0 1px 10px 1px rgba(115, 108, 203, 0.1);
    background-color: #f1f1f1;
}
[data-theme=dark_blue] .page-title .title-text {
	/* color: var(--third-color); */
    color: #0e2a43;
	font-size: 22px;
}
[data-theme=dark_blue] .widget .widget-head .widget-title,
[data-theme=dark_blue] .section-head,
[data-theme=dark_blue] .comment-input-header {
    font-size: 14px;
	/* color: var(--third-color); */
    color: #0e2a43!important;
}

/*Main Section */
[data-theme=dark_blue] .list-row-head {
    background-color: #f3f6f9;
    border-bottom: 1px solid #e4e4e4 !important;
}

/* Buttons */
[data-theme=dark_blue] .btn-primary {
    background-color: var(--main-color);
    border-color: var(--main-color);
}
[data-theme=dark_blue] .control-label, .grid-heading-row {
    font-size:13px;
}
[data-theme=dark_blue] .btn-default:hover,
[data-theme=dark_blue] .btn-default:focus, .btn-default.focus,
[data-theme=dark_blue] .btn-default:active, .btn-default.active,
[data-theme=dark_blue] .open > .dropdown-toggle.btn-default {
    background-color: var(--third-color) !important;
    color: #fff !important;
}
[data-theme=dark_blue] .btn-default:hover svg{
    stroke: #fff !important;
    fill: #fff !important;
    color: #fff !important;
}
[data-theme=dark_blue] .btn-primary:hover,
[data-theme=dark_blue] .btn-primary:focus, .btn-primary.focus,
[data-theme=dark_blue] .btn-primary:active, .btn-primary.active,
[data-theme=dark_blue] .open > .dropdown-toggle.btn-primary {
    background-color: var(--main-color) !important;
    border-color: var(--main-color) !important;
}
[data-theme=dark_blue] .btn-secondary,
[data-theme=dark_blue] .btn-secondary:focus, .btn-secondary.focus,
[data-theme=dark_blue] .btn-secondary:active, .btn-secondary.active,
[data-theme=dark_blue] .open > .dropdown-toggle.btn-secondary {
    background-color: var(--third-color);
    color: #fff;
}

[data-theme=dark_blue] .btn-secondary:hover{
    background-color: var(--main-color);
    color: #fff;
}
[data-theme=dark_blue] .indicator-right.blue:after, .indicator.blue:before {
    background: var(--third-color);
}

/*Other Components*/
[data-theme=dark_blue] input[type=checkbox]:checked:before {
    color: var(--third-color);
}
[data-theme=dark_blue] input[type=checkbox]:checked{
    background: var(--main-color);
}
[data-theme=dark_blue] .frappe-control .ql-editor:not(.read-mode){
    background: var(--widget-bg);
}
[data-theme=dark_blue] .input-with-feedback,
[data-theme=dark_blue] .like-disabled-input{
    background-color: #e8eafcad;
}
[data-theme=dark_blue] .document-link-badge span{
    background-color: var(--main-color) !important;
    color: #fff;
}

[data-theme=dark_blue] .document-link-badge{
    background-color: var(--widget-bg)!important;
}
[data-theme=dark_blue] .document-link button{
    background: var(--third-color) !important;
    color: #fff !important;
}

[data-theme=dark_blue] .document-link button svg{
    fill: white;
    stroke: white;
}
[data-theme=dark_blue] .indicator-pill.gray,
[data-theme=dark_blue] .indicator-pill-right.gray,
[data-theme=dark_blue] .indicator-pill-round.gray {
    background: var(--third-color);
    color: #fff;
}


[data-theme=dark_blue] .indicator-pill.yellow{
    background:#e17a28;
    color: white;
}
[data-theme=dark_blue] .indicator-pill.green,
[data-theme=dark_blue] .indicator-pill-right.green,
[data-theme=dark_blue] .indicator-pill-round.green {
    color: var(--main-color);
}

[data-theme=dark_blue] .notifications-seen>svg{
    stroke: #fff !important;
    fill: #fff!important;
}
[data-theme=dark_blue] .notifications-seen>svg:hover{
    stroke: #e17a28 !important;
    fill: #e17a28!important;
}
[data-theme=dark_blue] .icon{
    stroke: #0e2a43 !important;
    fill: none!important;
}
[data-theme=dark_blue] .search-bar>svg{
    stroke: #02BBB6 !important;
    fill: none!important;
}

[data-theme=dark_blue] #page-Workspaces {
    /* background: var(--widget-bg); */
    background-color: #F1F1F1;
}

/* my edit  */

[data-theme=dark_blue] .custom-actions > .btn-default{
    background: #E17A28 !important;
    color: white !important;
}


[data-theme=dark_blue] .spacer{
    background:none!important;
    border: none!important;
    box-shadow: none !important; 
}

[data-theme=dark_blue] .icon-xs{
    color:white;
}

[data-theme=dark_blue] .btn-xs:hover{
    background-color:#e17a28 !important;
}
[data-theme=dark_blue] .widget-control > .filter-chart:hover{
    background-color:#e17a28 !important;
}
[data-theme=dark_blue] .widget-control > .filter-chart{
    background-color:#6dd4ac !important;
}

/* when i create any this quike create */
[data-theme=dark_blue] .standard-actions > .btn-primary{
    background-color:#e17a28 !important;
}
[data-theme=dark_blue] .modal-content {
    border: none !important;
}
[data-theme=dark_blue] .modal-content > .modal-header{
    background-color:#0e2a43 !important;
}
[data-theme=dark_blue] .modal-content > .modal-footer{
    background-color:#19a76d !important;
}



/* in the list document for doctype */

[data-theme=dark_blue] .page-form{
    background-color:#19a76d !important;
}
[data-theme=dark_blue] .page-form  input[type=text]{
    color: white;
}
[data-theme=dark_blue] .page-form  select[type=text]{
    color: white;
}

[data-theme=dark_blue] .page-form > .filter-section  button {
    background-color:#e17a28 !important;
    border: none !important;
}
[data-theme=dark_blue] .page-form > .filter-section > .btn-group .btn {
    background-color:#e17a28 !important;
    outline: none !important;
}

[data-theme=dark_blue] .btn-new-doc{
    background-color:#e17a28 !important;
}
[data-theme=dark_blue] .btn-new-doc:hover{
    background-color:#6dd4ac !important;
}

/* button action above the page list document */
[data-theme=dark_blue] .page-actions > .custom-actions button{
    background-color:#d1d1d1 !important;
    color:rgb(0, 0, 0)!important;
}
[data-theme=dark_blue] .page-actions > .custom-actions button :hover{
    background-color:#204E84 !important;
    color:rgb(0, 0, 0)!important;
}
[data-theme=dark_blue] .page-actions > .standard-actions  > .menu-btn-group button {
    background-color:#d1d1d1 !important;
    color:white!important;
}
[data-theme=dark_blue] .page-actions > .standard-actions  > .menu-btn-group button:hover {
    background-color:#204E84 !important;
    color:white!important;
}
[data-theme=dark_blue] .page-actions > .standard-actions  > .icon-btn{
    background-color:#f1f1f1 !important;
    color:white!important;
}



/* in create new document  */
/* the nav bar  204E84 */
[data-theme=dark_blue] .form-tabs-list{
    background-color: #204E84!important;
    color: white!important;
}
[data-theme=dark_blue] .nav-link.active {
    
    color: #e17a28!important;
}
[data-theme=dark_blue] .form-tabs-list a{
