}
[data-theme=dark_blue] .form-tabs-list a:hover{
    color: #e17a28!important;
    background-color: none !important;
}
[data-theme=dark_blue]   .control-input{
    color: white!important;
    background-color: #19A66E !important;
}
[data-theme=dark_blue]   .like-disabled-input{
    color: white!important;
    background-color: #6dd4be94 !important;
}
[data-theme=dark_blue] .grid-heading-row{
    color: white!important;
    background-color: #19A66E !important;
}
[data-theme=dark_blue] input[type=checkbox]:hover{
    background-color: #e17a28;
}
[data-theme=dark_blue] input[type=checkbox]:checked{
    background-color: #e17a28;
}
[data-theme=dark_blue] .awesomplete> #awesomplete_list_3 li:hover{
    background-color: #FFFFFF!important;
}
[data-theme=dark_blue] .grid-field button{
    background-color: #00A1ED!important;
}
[data-theme=dark_blue] .grid-field button:hover{
